<script>
export default {
  name: "AddTeacherModal",
  data() {
    return {
      dialog: true,
      loading: false,
      name: '',
      calendarUrl: ''
    }
  },
  methods: {
    closeModal() {
      this.dialog = false;
      this.$router.push('/group');
    },
    saveGroup() {
      this.loading = true;
      this.$store.dispatch('addGroup', {
        name: this.name,
        calendarUrl: this.calendarUrl
      }).then(() => {
        this.loading = false;
        this.closeModal();
      })
    }
  }
}
</script>

<template>
  <v-dialog
      v-model="dialog"
      max-width="500px"
  >
    <v-card>
      <v-card-title>
        <span class="headline">Add Group</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field
              label="Name"
              required
              v-model="name"
              :disabled="loading"
          ></v-text-field>
          <v-text-field
              label="ADE Calendar URL"
              required
              v-model="calendarUrl"
              :disabled="loading"
          ></v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="blue darken-1"
            text
            @click="closeModal"
        >Cancel
        </v-btn>
        <v-btn
            color="blue darken-1"
            text
            @click="saveGroup"
            :loading="loading"
        >Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

